<template>
  <div class="Mv">
    <div class="music-container">
      <!-- 最新 MV -->
      <div class="tab">
        <span
          :class="['item',type==index? 'active': '']"
          v-for="(item, index) in new_tab"
          :key="index"
          @click="getNewMv(index)"
        >{{item}}</span>
      </div>
      <div class="mv-content">
        <mv-item
          class="item"
          v-for="(item, index) in newmv_list"
          :key="index"
          :playListId="item.id"
          :cover="item.cover"
          :name="item.name"
          :artistName="item.artistName"
          :artists="item.artists"
          :playCount="item.playCount"
          @mvDetail="goMvDetail"
        >
          </mv-item>
      </div>

      <!-- 推荐 MV -->
      <div class="introduce-mv mv-header">
        <h3>推荐 MV</h3>
      </div>
      <div class="mv-content">
        <mv-item
          class="item"
          v-for="(item, index) in introducemv"
          :key="index"
          :playListId="item.id"
          :cover="item.picUrl"
          :name="item.name"
          :artistName="item.artistName"
          :artists="item.artists"
          :playCount="item.playCount"
          @mvDetail="goMvDetail"
        >
          </mv-item>
      </div>

    </div>
  </div>
</template>
<script>
import mvItem from "../components/item/mvItem";
import tools from "../tools/formatData.js";

import { req_new_mv, req_introduce_mv } from "../network/mv/index.js";

export default {
  name: "Mv",
  components: {
    mvItem,
  },
  data() {
    return {
      type: 0,
      paginationShow: false,

      new_tab: ["内地", "港台", "欧美", "日本", "韩国"],
      newmv_list: [],
      newmv_Num: 12,

      introducemv: [],
    };
  },
  watch: {
    newmv_list: function() {
      this.newmv_list.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 1);
        arr[index].playCount = value;
      });
    },
    introducemv: function() {
      this.introducemv.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 1);
        arr[index].playCount = value;
      });
    },
  },
  methods: {
    // 跳转到 MV
    goMvDetail(id) {
      console.log(id);
      let time = new Date().getTime();
      this.$router.push({
        path: `/mvdetail/${time}`,
        query: { id },
      });
    },

    // 获取最新歌单
    getNewMv(index = this.type) {
      this.type = index;
      let params = {
        limit: this.newmv_Num,
        area: this.new_tab[this.type],
      };
      req_new_mv(this, params)
        .then(res => {
          this.newmv_list = res.data;
        })
        .catch(error => {
          console.log("获取最新 MV 出现错误", error);
        });
    },

    // 获取推荐歌单
    getIntroduceMv() {
      req_introduce_mv(this)
        .then(res => {
          this.introducemv = res.result;
        })
        .catch(error => {
          console.log(error);
        });
    },
  },
  created() {
    this.getNewMv(0);
    this.getIntroduceMv();
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
.Mv {
  .music-container {
    .tab {
      @include flex(flex-start);
      margin: 12px 0px;
      padding-bottom: 12px;
      border-bottom: 1px solid rgba($color: #000000, $alpha: 0.4);
      .item {
        display: block;
        padding: 10px, 12px;
        margin-right: 12px;
        text-align: center;
        @include font(16px, 20px, $colorB);
        &:hover {
          color: $colorA;
          cursor: pointer;
        }
      }
      .active {
        color: $colorA;
      }
    }
    .mv-content {
      @include flex();
      flex-wrap: wrap;
    }
    .mv-header {
      h3 {
        font-size: 20px;
        margin-top: 45px;
        margin-bottom: 20px;
      }
    }
  }
}
</style>