<template>
    <div class="mv-item" @click="getDetail(playListId)">
        <img v-lazy="cover" alt="">
        <p class="name" @click="getDetail(playListId)">{{name}} / {{artistName}}</p>
        <div class="artists">
            <div class="artist" v-for="(art, i) in artists" :key="i" @click="getSinger(art.id)">{{art.name}}</div>
        </div>
        <div class="mask">
                <div class="count">播放量 {{playCount}}</div>
        </div>
    </div>
</template>
<script>
export default {
  name: "playList-item",
  props: {
    // 歌单 id
    playListId: Number,
    // 图片地址
    cover: String,
    // 歌单名字
    name: String,
    // 歌手
    artistName: String,
    // 其他歌手
    artists: Array,
    // 播放量
    playCount: String,
  },
  watch: {
  },
  methods: {
    getSinger(id) {
      console.log("歌手id", id);
    },
    getDetail(id) {
       this.$emit("mvDetail", id);
    }
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
.mv-item {
  position: relative;
  width: 280px;
  margin-bottom: 20px;
  background-color: $colorD;
  padding-bottom: 6px;
  box-sizing: border-box;
  img {
    display: block;
    width: 100%;
    height: 140px;
  }
  .name {
    margin-top: 6px;
    @include font(16px, 20px, $colorC);
    padding-left: 3px;
    @include fontHidden();
    cursor: pointer;
    &:hover {
      color: $colorA;
    }
  }
  .artists {
    @include flex(flex-start);
    padding-left: 3px;
    margin-top: 12px;
    .artist {
      margin-right: 6px;
      cursor: pointer;
      &:hover {
        color: $colorA;
      }
    }
  }
  .mask {
    @include position();
    @include setMask(45px, 45px);
    .count {
      height: 40px;
      box-sizing: border-box;
      padding-left: 12px;
      background-color: rgba(0, 0, 0, 0.3);
      @include font(14px, 40px, $colorD);
    }
  }
  &:hover {
    box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.3);
    transform: translate3d(0, -2px, 0);
    .mask {
      opacity: 1;
    }
  }
}
</style>